import * as React from "react";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import { NavLink } from "react-router-dom";
import { Icon } from "@mui/material";
import { useSelector } from "react-redux";
import Lispost from "./Lispost";

export default function TextMobileStepper() {
  const user = useSelector((state) => state.user?.user);

  // Exemple d'utilisation
  // Affichera la date actuelle au format JJ/MM/AAAA (par exemple, 14/03/2023 pour le 14 mars 2023)

  return (
    <Box
      sx={{
        backgroundImage: `url('./Tori/soprano18.jpg')`,
        backgroundPosition: "center",
        height: "100vh",
        width: "100%",
        backgroundSize: "cover",
        backgroundColor:
          "rgba(128, 128, 128, 1)" /* La couleur de nuance en gris semi-transparent */,
        backgroundBlendMode: "overlay",
      }}
    >
      <NavLink to="/home">
        <Icon>
          <ArrowBackIcon />
        </Icon>
      </NavLink>
      <h2 style={{ textAlign: "center" }}>
        Note d'valuation obtenue lors des compositions
      </h2>

      {user.souscription === "" ? (
        <>
          <Typography variant="subtitle2">
            Compte non souscrire, veuilllez vous souscrire pour profiter des
            avantages qu'offre le groupe fanfare la Grce Parle. Shalom!
          </Typography>
          <Button variant="contained" size="small">
            <a
              style={{ textDecoration: "none" }}
              href="https://me.fedapay.com/souscription_1234"
              target="blank"
            >
              Souscrire
            </a>
          </Button>
          {/* <Typography variant='subtitle2'>Aprs la souscription,veuillez vous inscire sur FINANFA en cliquant sur ce lien : 
            <Button variant="filed" size="small">
        <a
          style={{ textDecoration: "none" }}
          href="https://network.finanfa.africa/login/signup/7z3KxBg5DArlb9eQ8JLn"
          target="blank"
        >
        Inscription sur FINANFA
        </a>
          </Button> </Typography> */}
        </>
      ) : (
        user?.average !== "" && (
          <Box>
            {/* <Typography variant='h6' sx={{textAlign:'center'}} > Evaluation </Typography> */}
            <h3>Note obtenue : {user.average}/20 </h3>

            {user.sexe === "Masculin" && user.average < "10" && (
              <>
                <Typography variant="subtitle2">
                  Si vous avez obtenu une moyenne en dessous de 10, il est
                  important de prendre conscience que cela peut tre un signe
                  d'alarme concernant votre performance musicale au sein du
                  groupe fanfare. Les notes ne refltent pas toujours la valeur
                  d'une personne, mais dans le contexte musical, elles sont un
                  indicateur important de la comprhension des partitions et de
                  la performance globale d'un musicien.
                </Typography>
                <Typography variant="subtitle2">
                  Obtenir une note en dessous de 10 peut tre un signal que vous
                  ne comprenez pas bien la partition ou que vous ne travaillez
                  pas assez dur pour jouer avec prcision et musicalit. Si vous
                  ne ragissez pas rapidement pour amliorer votre performance,
                  vous risquez de vous retrouver dans une situation difficile au
                  sein du groupe. Il est important de se rappeler que le monde
                  de la musique est trs comptitif et que les rsultats que
                  vous obtenez peuvent avoir un impact significatif sur votre
                  avenir musical. Les musiciens qui ont des comptences
                  musicales limites ont tendance  avoir des opportunits
                  professionnelles plus limites et peuvent avoir des
                  difficults  progresser dans leur carrire.
                </Typography>
                <Typography variant="subtitle2">
                  Il est donc essentiel de prendre des mesures pour amliorer
                  votre performance musicale ds que possible. Cela peut inclure
                  l'adoption d'une approche plus stratgique pour tudier les
                  partitions, la recherche d'aide supplmentaire auprs d'un
                  enseignant ou d'un membre expriment du groupe, et
                  l'engagement plus actif lors des rptitions.
                </Typography>
              </>
            )}
            {user.sexe === "Masculin" &&
              user.partition === "Percussion" &&
              user.average < "10" && (
                <Typography variant="subtitle2">
                  En tant que percussionniste dans notre groupe fanfare, nous
                  avons remarqu que votre jeu de tambours est solide, mais nous
                  sommes proccups par votre manque d'intrt pour la thorie
                  musicale. Vous ne pouvez pas ignorer l'importance de la
                  thorie musicale, car elle est essentielle pour comprendre les
                  nuances de la musique et pour amliorer votre jeu. Nous vous
                  encourageons  prendre le temps d'tudier la thorie musicale
                  et  appliquer ces connaissances dans votre pratique. Cela
                  vous aidera  exceller dans votre jeu de percussion et 
                  contribuer de manire plus significative  notre groupe.
                  N'oubliez pas que la pratique et la thorie musicale vont de
                  pair pour crer une exprience musicale riche et complte.
                </Typography>
              )}
            {(user.sexe === "Masculin" || user.sexe === "Fminin") &&
              user.partition === "Percussion" &&
              user.average > "10" && (
                <Typography variant="subtitle2">
                  En tant que percussionniste, vous apportez une contribution
                  essentielle  notre groupe fanfare. Mais en plus de votre
                  excellent travail sur les tambours, nous sommes galement
                  impressionns par votre connaissance de la thorie musicale.
                  Votre capacit  comprendre les nuances de la musique et 
                  appliquer ces connaissances dans votre jeu est un atout
                  inestimable pour notre groupe. Continuez  approfondir vos
                  connaissances de la thorie musicale et  trouver des moyens
                  de les appliquer dans votre jeu de percussion. Nous sommes
                  fiers de vous avoir comme membre de notre groupe et nous
                  sommes impatients de voir o votre talent et votre travail
                  acharn vous mneront.
                </Typography>
              )}

            {user.sexe !== "Masculin" && user.average < "10" && (
              <Typography variant="subtitle2">
                En tant que musicienne dans un groupe fanfare, vous savez que
                chaque note compte. Obtenez une note en dessous de 10 peut tre
                proccupant, mais cela peut tre un signe que vous devez
                travailler plus dur et vous concentrer sur votre pratique.
                Utilisez cette exprience pour vous motiver  vous amliorer et
                 exceller dans votre musique.
              </Typography>
            )}

            {user.average >= "10" && user.average <= "14" && (
              <Typography variant="subtitle2">
                Vous avez obtenu une note entre 10 et 15, flicitations pour vos
                efforts et votre russite  atteindre un niveau de comptence de
                base dans votre performance musicale au sein du groupe fanfare.
                C'est un bon dbut, mais cela ne signifie pas que vous ne pouvez
                pas continuer  vous amliorer. Il est important de se rappeler
                que la musique est une discipline qui ncessite de la pratique
                et de l'engagement pour atteindre un niveau lev de comptence.
                Les notes que vous obtenez peuvent tre un indicateur de votre
                performance, mais elles ne refltent pas toujours l'ensemble de
                vos efforts et de votre potentiel. Si vous voulez amliorer
                votre performance musicale, il est important de travailler sur
                vos comptences de base telles que la comprhension de la
                partition et la technique de jeu. Cela peut tre fait en
                travaillant avec un enseignant ou un membre expriment du
                groupe qui peut vous donner des conseils et des astuces pour
                amliorer votre performance.
              </Typography>
            )}
            {user.average > "14" && (
              <Typography variant="subtitle2">
                Flicitations pour avoir obtenu une note au-dessus de 15 ! C'est
                une preuve de votre travail acharn et de votre talent en tant
                que musicien dans le groupe fanfare. Continuez  vous concentrer
                sur votre pratique et votre apprentissage pour maintenir et
                amliorer votre niveau de performance. Vous tes un exemple pour
                vos camarades de groupe, alors continuez  inspirer et 
                encourager les autres membres  atteindre leur plein potentiel.
                N'oubliez pas de prendre le temps de clbrer vos succs, tout
                en restant humble et en cherchant toujours  vous amliorer.
                Continuez  travailler avec dtermination et passion, et vous
                continuerez  exceller dans votre musique.
              </Typography>
            )}
          </Box>
        )
      )}
      {user.average === "" && (
        <Typography variant="subtitle2">
          Aucune note disponible pour le moment{" "}
        </Typography>
      )}

      <Typography textAlign="center" variant="h6">
        Communiqu urgent{" "}
      </Typography>

      <Lispost />
    </Box>
  );
}
